import React, { Component } from 'react';
import $script from 'scriptjs';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import MenuItem from '@material-ui/core/MenuItem';
import NativeSelect from '@material-ui/core/NativeSelect';
import FormHelperText from '@material-ui/core/FormHelperText';
import Select from '@material-ui/core/NativeSelect';
import FormControl from '@material-ui/core/FormControl';
import InputLabel from '@material-ui/core/InputLabel';
import Input from '@material-ui/core/Input';
import moment from 'moment';
import './App.css';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';

const CLIENT_ID =
  '626897179786-u8rj411ic3irrm9lp5poj1gi0q8ckurf.apps.googleusercontent.com';
const API_KEY = 'AIzaSyB6G-3wgN7eBBJak-rl6EVvtkAmvSC5wVA';

const DISCOVERY_DOCS = [
  'https://sheets.googleapis.com/$discovery/rest?version=v4'
];

const SCOPES = 'https://www.googleapis.com/auth/spreadsheets';

const styles = theme => ({
  container: {
    display: 'flex',
    flexWrap: 'wrap'
  },
  textField: {
    marginBottom: theme.spacing.unit,
    marginLeft: theme.spacing.unit,
    marginRight: theme.spacing.unit,
    width: '100%'
  },

  submit: {
    marginTop: theme.spacing.unit,
    marginLeft: theme.spacing.unit,
    marginRight: theme.spacing.unit,
    width: '100%'
  },
  menu: {
    width: 200
  }
});

class App extends Component {
  state = {
    transactionToSubmit: {
      employee: '',
      item: '',
      price: '',
      client: '',
      gender: '',
      date: moment().format('YYYY-MM-DDThh:mm')
    },
    transactions: [],
    items: []
  };

  componentDidMount() {
    $script('https://apis.google.com/js/api.js', () => {
      this.handleClientLoad();
    });
  }

  handleClientLoad = () => {
    window.gapi.load('client:auth2', this.initClient);
  };

  initClient = () => {
    window.gapi.client
      .init({
        apiKey: API_KEY,
        clientId: CLIENT_ID,
        discoveryDocs: DISCOVERY_DOCS,
        scope: SCOPES
      })
      .then(() => {
        this.setState({ loaded: true });
        this.listMajors();
        this.fetchItems();
        window.gapi.auth2
          .getAuthInstance()
          .isSignedIn.listen(this.updateSigninStatus);

        this.updateSigninStatus(
          window.gapi.auth2.getAuthInstance().isSignedIn.get()
        );
      });
  };

  updateSigninStatus = isSignedIn => {
    this.setState({ isSignedIn });
  };

  handleAuthClick(event) {
    window.gapi.auth2.getAuthInstance().signIn();
  }

  handleSignoutClick(event) {
    window.gapi.auth2.getAuthInstance().signOut();
  }

  handleChange = field => event => {
    const transactionToSubmit = {
      ...this.state.transactionToSubmit,
      ...{ [field]: event.target.value }
    };

    if (field === 'item') {
      transactionToSubmit.price = this.state.items.find(
        i => i.name === event.target.value
      ).price;
    }

    this.setState({ transactionToSubmit });
  };

  handleSubmit = event => {
    event.preventDefault();
    const range = 'entrada!A1:G';
    const params = {
      spreadsheetId: '1NrLTgE3K_o8LYXdfH1dp4kDMAckoQZtlSaYZFGn8Ick',
      range,
      valueInputOption: 'USER_ENTERED'
    };

    const {
      employee,
      price,
      item,
      date,
      client,
      gender
    } = this.state.transactionToSubmit;
    const formatedDate = moment(date, 'YYYY-MM-DDThh:mm').format(
      'DD/MM/YYYY h:mm:ss'
    );
    const valueRangeBody = {
      majorDimension: 'ROWS',
      range,
      values: [
        [employee, item, gender, price, 'deve', client, 30, formatedDate]
      ]
    };

    const request = window.gapi.client.sheets.spreadsheets.values.append(
      params,
      valueRangeBody
    );
    request.then(
      response => {
        // TODO: Change code below to process the `response` object:
        console.log(response.result);
        this.listMajors();
      },
      reason => {
        console.error('error: ' + reason.result.error.message);
      }
    );
  };

  listMajors() {
    window.gapi.client.sheets.spreadsheets.values
      .get({
        spreadsheetId: '1NrLTgE3K_o8LYXdfH1dp4kDMAckoQZtlSaYZFGn8Ick',
        range: 'entrada!A2:H'
      })
      .then(
        response => {
          const transactions = response.result.values;
          this.setState({ transactions });
        },
        response => {
          this.setState({ error: response.result.error });
        }
      );
  }

  fetchItems = () => {
    window.gapi.client.sheets.spreadsheets.values
      .get({
        spreadsheetId: '1NrLTgE3K_o8LYXdfH1dp4kDMAckoQZtlSaYZFGn8Ick',
        range: 'servicos!A2:B'
      })
      .then(
        response => {
          const items = response.result.values.map(([name, price]) => ({
            name,
            price
          }));
          this.setState({ items });
        },
        response => {
          this.setState({ error: response.result.error });
        }
      );
  };

  lastTransactions = () => this.state.transactions.reverse().slice(0, 10);

  render() {
    const { classes } = this.props;
    if (!this.state.loaded) {
      return 'Carregando...';
    }
    return (
      <div className="App">
        <h1>Salão</h1>

        {this.state.isSignedIn ? (
          <button id="signout_button" onClick={this.handleSignoutClick}>
            Sign Out
          </button>
        ) : (
          <button id="authorize_button" onClick={this.handleAuthClick}>
            Authorize
          </button>
        )}

        <br />
        <br />
        <br />

        <form
          className={classes.container}
          onSubmit={this.handleSubmit}
          autoComplete="off"
        >
          <FormControl required className={classes.textField}>
            <InputLabel htmlFor="item">Item</InputLabel>
            <Select
              native
              value={this.state.age}
              onChange={this.handleChange('item')}
              name="item"
              inputProps={{
                id: 'item'
              }}
            >
              <option value="" />
              {this.state.items.map(option => (
                <option key={option.name} value={option.name}>
                  {option.name}
                </option>
              ))}
            </Select>
          </FormControl>

          <TextField
            value={this.state.transactionToSubmit.price}
            label="Preço"
            type="number"
            onChange={this.handleChange('price')}
            className={classes.textField}
            required
          />

          <FormControl required className={classes.textField}>
            <InputLabel htmlFor="employee">Funcionario</InputLabel>
            <Select
              native
              value={this.state.age}
              onChange={this.handleChange('employee')}
              name="employee"
              inputProps={{
                id: 'employee'
              }}
            >
              <option value="" />
              {['Maurilio', 'Victor'].map(option => (
                <option key={option} value={option}>
                  {option}
                </option>
              ))}
            </Select>
          </FormControl>

          <TextField
            value={this.state.transactionToSubmit.client}
            label="Cliente"
            onChange={this.handleChange('client')}
            className={classes.textField}
          />

          <FormControl required className={classes.textField}>
            <InputLabel htmlFor="gender">Genero</InputLabel>
            <Select
              native
              value={this.state.age}
              onChange={this.handleChange('gender')}
              name="gender"
              inputProps={{
                id: 'gender'
              }}
            >
              <option value="" />
              {['M', 'F'].map(option => (
                <option key={option} value={option}>
                  {option}
                </option>
              ))}
            </Select>
          </FormControl>

          <TextField
            value={this.state.transactionToSubmit.date}
            label="Data"
            type="datetime-local"
            onChange={this.handleChange('date')}
            className={classes.textField}
          />

          <br />
          <Button
            type="submit"
            variant="contained"
            color="primary"
            className={classes.submit}
          >
            Salvar
          </Button>
        </form>
        <ul>
          {this.lastTransactions().map((transaction, key) => (
            <li key={key}>{transaction.join(',')}</li>
          ))}
        </ul>
      </div>
    );
  }
}

App.propTypes = {
  classes: PropTypes.object.isRequired
};

export default withStyles(styles)(App);



